@media only screen and (max-width: 768px) { 
  .neum-form {
    padding: 1rem !important;
  }
}

.bg-grey-neu {
  background-color: #e6e7ee;
}

.neum-container {
  background-color: #e6e7ee;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 2rem;
}

.neum-form {
  padding: 2rem;
  border-radius: 8px;
  background: #e6e7ee;
  border-radius: 0.55rem;
  border: 0.0625rem solid #d1d9e6 !important;
  box-shadow: 6px 6px 12px #b8b9be, -6px -6px 12px #fff;
  margin: 2rem;
  margin: auto;
  box-sizing: border-box;
}

.form-input-neu,
.dropzone-area-neu {
  display: block;
  width: 100%;
  padding: 0.75rem;
  font-size: 1rem;
  font-weight: 300;
  line-height: 1.5;
  color: #44476a;
  box-sizing: border-box;
  background-color: #e6e7ee;
  background-clip: padding-box;
  border: 0.0625rem solid #d1d9e6;
  border-radius: 0.55rem;
  box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #fff;
  transition: all 0.3s ease-in-out;
}

.dropzone-area-neu {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
}

.form-input-neu:focus-visible,
.dropzone-area-neu:focus-visible {
  outline: none;
}

.neum-element {
  box-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #fff;
  color: #31344b;
  background-color: #e6e7ee;
  border: 0.0625rem solid #d1d9e6;
  border-radius: 0.55rem;
}

.hover-neum-element {
  box-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #fff;
  color: #31344b;
  background-color: #e6e7ee;
  border: 0.0625rem solid #d1d9e6;
  border-radius: 0.55rem;
}

.hover-neum-element:hover {
  color: #31344b;
  background-color: #e6e7ee;
  border-color: #e6e7ee;
  box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #fff;
}

.neum-form button.neu, button.neu {
  cursor: pointer;
  margin: 1rem 0rem;
  display: block;
  width: 100%;
  transition: all 0.2s ease;
  letter-spacing: 0.025em;
  font-size: 1rem;
  box-shadow: 3px 3px 6px #b8b9be, -3px -3px 6px #fff;
  color: #31344b;
  background-color: #e6e7ee;
  border: 0.0625rem solid #d1d9e6;
  padding: 0.55rem 0.95rem;
  font-size: 1rem;
  line-height: 1.5;
  border-radius: 0.55rem;
}

.neum-form button.neu:hover, button.neu:hover {
  color: #31344b;
  background-color: #e6e7ee;
  border-color: #e6e7ee;
  box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #fff;
}

.form-check-neu .checkbox-neu {
  opacity: 0;
  position: absolute;
  z-index: 1;
  cursor: pointer;
}

.form-check-neu .checkbox-label-neu {
  display: inline-block;
  position: relative;
  padding-left: 1.375rem;
  font-weight: 400;
  line-height: 16px;
  margin-bottom: 0;
  transition: all 0.2s ease;
  color: #44476a;
}

.form-check-neu .checkbox-label-neu::before {
  content: " ";
  display: inline-block;
  position: absolute;
  width: 15px;
  height: 15px;
  left: 0;
  top: 0;
  background-color: #e6e7ee;
  opacity: 1;
  font-size: 0.6rem;
  margin-top: 0;
  line-height: 1.6;
  box-shadow: 1px 1px 2px #aeafb3, -1px -1px 2px #fff;
}

.checkbox-neu:checked + .checkbox-label-neu::after {
  content: "";
  display: inline-block;
  position: absolute;
  width: 15px;
  height: 15px;
  left: -1px;
  top: -1px;
  border: 1px solid;
  transition: all 0.2s ease;
  border-radius: 2px;
  background-color: #c9c8c8;
  border-color: #ffffff00;
  box-shadow: inset 2px 2px 5px #b8b9be, inset -3px -3px 7px #e1e1e1;
}
