body {
  margin: 0;
  font-family: "Open Sans", sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media only screen and (max-width: 768px) {
  .title {
    font-size: 1.6rem !important;
  }

  .w-sm,
  .w-xs {
    width: 50% !important;
    margin: .2rem 0rem !important;
    font-size: .8rem;
  }

  .form-row {
    flex-wrap: wrap;
  }

  .form-input-50 {
    width: 100% !important;
  }

  .form-input-50 input {
    width: 100% !important;
  }
}

.link-item {
  color: #0a0a0a;
  text-decoration: none;
  font-size: 1.5rem;
  padding: 5px 0;
  letter-spacing: 1px;
  border-bottom: 1px solid #000;
  cursor: pointer;
}

.title {
  font-size: 3rem;
  font-weight: bold;
  color: #0a0a0a;
}

.bold {
  font-weight: 600;
}

.bolder {
  font-weight: 900;
}

.font-normal {
  font-size: 1rem;
}

.font-big {
  font-size: 2rem;
}

.form-input {
  margin: 1rem 0rem;
}

.form-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.form-input-50 {
  margin: 1rem 0rem;
  width: 50%;
}

.form-input-50 input {
  width: 97%;
}

.ml-auto {
  margin-left: auto;
}

.mr-auto {
  margin-right: auto;
}

.m-auto {
  margin: auto;
}

.d-inline {
  display: inline-block;
}

.mx-1 {
  margin: 0rem 1rem;
}

.mx-2 {
  margin: 0rem 1rem;
}

.my-1 {
  margin: 1rem 0rem;
}

.my-2 {
  margin: 1rem 0rem;
}

.mt-1 {
  margin-top: 1rem;
}

.mt-2 {
  margin-top: 2rem;
}

.mt-3 {
  margin-top: 3rem;
}

.mt-4 {
  margin-top: 4rem;
}

.mb-1 {
  margin-bottom: 1rem;
}

.mb-2 {
  margin-bottom: 2rem;
}

.mb-3 {
  margin-bottom: 3rem;
}

.mb-4 {
  margin-bottom: 4rem;
}

.w-100 {
  width: 100%;
}

.w-xs {
  width: 100px;
}

.w-sm {
  width: 200px;
  margin: 0rem 1rem;
}

.error-text {
  color: red;
  margin: 0px !important;
  padding-top: .4rem;
  font-size: .8rem;
}

.invalid-input {
  background-color: #f3dada;
}

.disabled-button {
  background-color: white !important;
  border: 2px solid #ccc !important;
  cursor:default !important;
}

.disabled-button:hover {
  color: #bbb !important;
}
