@media only screen and (max-width: 768px) {
  .header-list {
    align-items: flex-start !important;
    justify-content: flex-start !important;
  }

  .header-list .links {
    padding: 2rem !important;
    font-size: 2.5rem !important;
    font-weight: 600px !important;
    margin-top: 3rem !important;
  }
}

.header-list {
  display: none;
  position: fixed;
  top: 0px;
  right: 0px;
  background-color: rgba(256, 256, 256, 0.95);
  width: 100vw;
  height: 100vh;
  z-index: 2;
  justify-content: center;
  align-items: center;
}

.header-list.show {
  display: flex;
}

.header-list .links {
  font-size: 5rem;
  font-weight: 900;
  list-style: none;
  margin-right: 2rem;
  transition: margin-right 1.2s;
}

.link-underline {
  position: relative;
}

.link-underline:after {
  position: absolute;
  content: "";
  width: 0%;
  height: 8px;
  background-color: black;
  top: 98%;
  left: 0%;
  transition: width 0.3s;
}
.link-item:hover .link-underline::after {
  width: 100%;
}

.header-list.show .links {
  margin-right: 0rem;
}

.header-list .links li {
  margin: 1.5rem 0rem;
}

.link-item {
  text-decoration: none;
  color: black;
}

.burger-menu-container {
  position: fixed;
  top: 0%;
  right: 2%;
  padding: 1rem;
  cursor: pointer;
  z-index: 3;
}

.burger-menu-container span {
  display: block;
  width: 30px;
  height: 3px;
  background-color: black;
  margin: 0.5rem 0rem;
  transition: transform 0.3s, opacity 0.2s, width 0.3s;
}

.burger-menu-container.opened .burger-solid-element {
  width: 38px;
}

.burger-menu-container.opened .burger-solid-element:nth-child(1) {
  transform: rotate(-45deg) translateY(11px) translateX(-5px);
}

.burger-menu-container.opened .burger-solid-element:nth-child(2) {
  opacity: 0;
}

.burger-menu-container.opened .burger-solid-element:nth-child(3) {
  transform: rotate(45deg) translateY(-11px) translateX(-5px);
}
